body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App{
  margin: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Work Sans', sans-serif;
  height: 87vh;
}

.stories{
  -webkit-filter: drop-shadow(0 10px 10px #888);
          filter: drop-shadow(0 10px 10px #888);
}

.left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-right: 10rem;
  -ms-flex-pack: start;
      justify-content: flex-start;
  max-width: 350px
}

.seeMore{
  padding: 2rem;
  color: #16161d;
  background: white;
  height: -webkit-fill-available;
}

.seeMore input, .seeMore textarea {
  display: block;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0.8rem;
  margin-bottom: 0.7rem;
  width: -webkit-fill-available;
  -webkit-transition: -webkit-filter 300ms ease-in-out;
  transition: -webkit-filter 300ms ease-in-out;
  -o-transition: filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out, -webkit-filter 300ms ease-in-out;
  font-size: 0.8rem;
  font-family: inherit
}

.seeMore input:focus, .seeMore textarea:focus {
  outline: none;
  -webkit-filter: drop-shadow(0 2px 5px #ccc);
          filter: drop-shadow(0 2px 5px #ccc)
}

.seeMore button{
  display: block;
  margin: auto;
  margin-top: 1rem;
  background: #16161d;
  -webkit-filter: drop-shadow(0 3px 5px #ccc);
          filter: drop-shadow(0 3px 5px #ccc);
  color: snow;
  border-radius: 5px;
  border: none;
  width: 200px; 
  padding: 0.5rem;
  font-size: 1rem;
  -webkit-transition: -webkit-filter 300ms ease-in-out;
  transition: -webkit-filter 300ms ease-in-out;
  -o-transition: filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out;
  transition: filter 300ms ease-in-out, -webkit-filter 300ms ease-in-out
}

.seeMore button:focus{
  outline: none
}

.seeMore button:hover{
  cursor: pointer;
  -webkit-filter: drop-shadow(0 5px 5px #aaa);
          filter: drop-shadow(0 5px 5px #aaa);
}

.seeMore a {
  display: block;
  font-size: 0.8rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-top: 2rem;  
}

code {
  font-family: 'Roboto Mono', monospace;
}

@media only screen and (max-width: 768px){
  .App{
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    height: auto;
  }

  .left{
    max-width: 100%;
    margin: 0;
    margin-bottom: 2rem;
  }
} 



